import React from 'react';
import styled from '@emotion/styled';
import { heightHeader, color } from './utility';
import { Header, Footer } from './component';
import {
  PageHome,
  PageRarity,
  PageRoadmap,
  PageTeam
} from './page';

function App() {
  const isDev = new URLSearchParams(window.location.search)?.get('devmode') === 'yesletmein' ? true : false;

  return isDev ? (
    <StyledApp className="app">
      <Header />
      <PageHome id="home" />
      <PageRoadmap id="roadmap" />
      <PageRarity id="rarity" />
      <PageTeam id="team" />
      <Footer />
    </StyledApp>)
    :
    <StyledComingSoon>Coming Soon</StyledComingSoon>
  ;
}

const StyledComingSoon = styled.h1`
  margin: 30px;
  text-align: center;
`;

const StyledApp = styled.div`
  height: 100%;
  min-height: 100%;
  min-width: 310px;
  color: ${color.font};
`;

export default App;
